import { Button, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material'
import React, { useEffect } from 'react'
import { useSelector } from 'react-redux'
import { HolidayClient } from '../../api/server';
import { useParams } from 'react-router-dom';

function GlobalHolidays(props) {
    const globalInfo = useSelector((state) => state.globalInfo)
    const [holidays, setHolidays] = React.useState([]);
    const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
    let { currentPhoneNumberId } = useParams();

    function optInHoliday(globalHolidayId){
        const client = new HolidayClient(globalInfo.apiRoot, undefined, globalInfo.accessToken);
        client.optInHoliday(globalHolidayId, currentPhoneNumberId).then(()=>{
            props.onReload();
        })
    }
    

    useEffect(() => {
        function reload() {
            const client = new HolidayClient(globalInfo.apiRoot, undefined, globalInfo.accessToken);
    
            client.getGlobalHolidays().then((data) => {
                setHolidays(data)
            })
        }

        reload();
    }, [globalInfo])


    return (
        <div>

            <TableContainer component={Paper}>
                <Table>
                    <TableHead>
                        <TableRow>
                        </TableRow>
                        <TableCell colSpan={5}>
                            <h1>Master Holiday List</h1>
                        </TableCell>
                    </TableHead>
                    <TableHead>
                        <TableRow>
                            <TableCell>
                                Store Closed?
                            </TableCell>
                            <TableCell>
                                Date
                            </TableCell>
                            <TableCell>
                                Name
                            </TableCell>
                            <TableCell>
                                Audio
                            </TableCell>
                            <TableCell>
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {holidays.map((row, index) => (
                            <React.Fragment key={row.holidayId}>
                                <TableRow >
                                    <TableCell>
                                        {row.isBusinessClosed ? "Yes" : "No"}
                                    </TableCell>
                                    <TableCell>
                                        {
                                            (new Date(row.fromDate.toString())).toLocaleDateString("en-us", options)
                                        }
                                        {
                                            row.fromDate.toString() !== row.toDate.toString() && <>&nbsp; To &nbsp;
                                                {
                                                    (new Date(row.toDate.toString())).toLocaleDateString("en-us", options)
                                                }
                                            </>
                                        }
                                    </TableCell>
                                    <TableCell>
                                        {row.holidayName}
                                    </TableCell>
                                    <TableCell>
                                        

                                        {
                                            row.audioFile &&
                                            <figure style={{ marginLeft: -10 }}>
                                                <audio
                                                    controls
                                                    src={globalInfo.apiRoot + "/" + row.audioFile.path}>
                                                    <a href={globalInfo.apiRoot + "/" + row.audioFile.path}>
                                                        Download audio
                                                    </a>
                                                </audio>
                                            </figure>
                                        }
                                    </TableCell>
                                    <TableCell>
                                        <Button variant='contained' onClick={() => { optInHoliday(row.holidayId) }}>Add To My Holidays</Button>
                                    </TableCell>

                                </TableRow>

                            </React.Fragment>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    )
}

export default GlobalHolidays